body {
    background-color: #f0f0f0;
}

.main-container {
    margin: 30px;
}

.list-container {
    margin-right: 20px;
    background-color: #fff;
    padding: 10px;
    box-shadow: 10px 7px 20px rgba(0, 0, 0, 0.1);
    max-width: 250px;
    width: 250px;
}

.list-group {
    margin-bottom: 20px;
}

.list-title {
    color: rgb(97, 0, 130);
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-item {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    border-bottom: solid 1px #f0f0f0;
    align-items: center;
    padding-bottom: 6px;
    padding-top: 6px;
}

button {
    border: none;
    color: #fff;
    font-weight: bold;
    background-color: rgb(92, 110, 244);
    font-size: 16px;
    padding: 8px;
}

input {
    font-size: 16px;
    padding: 8px;
    border: none;
    border-bottom: solid 1px rgb(92, 110, 244);
    outline: none;
    flex: 1;
}

button.disabled {
    color: rgb(114, 114, 114);
    background-color: rgb(170, 180, 255);
}

.chat-container {
    background-color: #fff;
    padding: 10px;
    box-shadow: 10px 7px 20px rgba(0, 0, 0, 0.1);
    flex: 1;
    display: flex;
    flex-direction: column;
}

.chat-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chat-content {
    white-space: pre-wrap;
    overflow: auto;
    height: calc(100vh - 120px);
}

.game-message {
    position: absolute;
    width: 300px;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 20px;
    text-align: center;
    left: 120px;
    top: 60px;
}
